.navigationBar {
  height: 64px;
  width: 100%;
  background-color: white;
  border: 1px solid rgb(220, 220, 220);
  /* position: fixed; */
}

.li {
  display: inline;
}

.icons {
  width: 32px;
  height: 32px;
  padding-left: 16px;
}


.contentsLeft {
  padding-left: 16px;
  float: left;
  transform: translateY(10%);
}

.contentsRight {
  padding-right: 16px;
  float: right;
  transform: translateY(50%);
}